import { Component, getAssetPath, h, Host, Prop } from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';
import { TpVideoThumbnailSize } from './utils/video-thumbnail.enum';

@Component({
  tag: 'tp-video-thumbnail',
  styleUrl: 'video-thumbnail.scss',
  assetsDirs: ['assets'],
  scoped: true,
})
export class TpVideoThumbnail {
  private iconPlay = getAssetPath('./assets/tp-play-circle.svg');

  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop({ reflect: true }) url?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) size?: TpVideoThumbnailSize;

  /**
   * todo
   */
  @Prop({ reflect: true }) value = 0;

  render() {
    const { color, url, value, iconPlay, size } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-video-thumbnail': true,
          [`tp-video-thumbnail--${size}`]: Boolean(size),
        })}
      >
        <img class="tp-video-thumbnail__img" src={url} />
        <div class="tp-video-thumbnail__container-icon">
          <ion-icon class="tp-video-thumbnail__icon" src={iconPlay}></ion-icon>
        </div>
        <ion-progress-bar
          color={color}
          class="tp-video-thumbnail__progress"
          value={value}
        ></ion-progress-bar>
      </Host>
    );
  }
}
