@use 'sass:map';

$tp-video-thumbnail-sizes: (
  lg: (
    properties: (
      max-width: 856px,
      max-height: 524px,
      padding: 16px,
      width-icon: 48px,
      height-icon: 48px,
    ),
  ),

  md: (
    properties: (
      max-width: 182px,
      max-height: 108px,
      padding: 8px,
      width-icon: 24px,
      height-icon: 24px,
    ),
  ),

  sm: (
    properties: (
      max-width: 112px,
      max-height: 68px,
      padding: 4px,
      width-icon: 24px,
      height-icon: 24px,
    ),
  ),

  xs: (
    properties: (
      max-width: 88px,
      max-height: 60px,
      padding: 4px,
      width-icon: 16px,
      height-icon: 16px,
    ),
  ),
);

:host {
  --tp-max-width: 856px;
  --tp-max-height: 524px;
  --tp-padding: 16px;
  --tp-width-icon: 48px;
  --tp-height-icon: 48px;
}

:host {
  display: block;
  max-width: var(--tp-max-width);
  max-height: var(--tp-max-height);
  width: 100%;
  height: auto;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.tp-video-thumbnail {
  &__img {
    width: 100%;
    height: auto;
  }

  &__container-icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: var(--tp-padding);
    background: rgba(var(--tp-color-neutral-1-rgb), 50%);
  }

  &__icon {
    color: var(--tp-color-neutral-10);
    width: var(--tp-width-icon);
    height: var(--tp-height-icon);
  }

  &__progress {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

//
// sizes
//

:host {
  @each $size-name, $size-value in $tp-video-thumbnail-sizes {
    &.tp-video-thumbnail--#{$size-name} {
      $properties-map: map.get($size-value, properties);

      @each $properties-name, $properties-value in $properties-map {
        --tp-#{$properties-name}: #{$properties-value};
      }
    }
  }
}

//
// color
//

:host(.tp-color) {
  .tp-video-thumbnail__progress {
    --background: rgba(var(--ion-color-base-rgb), 0.4);
    --progress-background: var(--ion-color-base);
  }
}
